
body {
  padding: 0;
  margin: 0;
  text-align: center;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 200%;
}
canvas {
  display: none;
}
#canvas canvas {
  display: block;
}
.screen {
  overflow-x: hidden;
}
#startScreen {
  height: 100vh;
  z-index: 1;
  background-color: #333333;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.pauseButton {
  margin: 10px auto;
}
.button {
  cursor: pointer;
  background-color: #dddddd;
  display: block;
  padding: 10px;
  width: 200px;
  border: 0;
  font-size: 0.8em;
  box-shadow: none;
  border-radius: 0px;
  color: #333333;
  font-family: 'VT323', monospace;
}
.button:hover {
  background-color: #ff0000;
  color: white;
}
label.button {
  cursor: pointer;
}
input[type='radio'] {
  opacity: 0;
  width: 0;
  height: 0;
  position: relative;
  top: 10px;
}
input[type='radio']:checked ~ label {
  background-color: #ff0000;
  color: white;
}
input[type='text']{
  cursor: auto;
}
input[type='number'] {
  cursor: auto;
}
#startScreen button:nth-child(2) {
  margin-bottom: 10px;
}
#startScreen button:nth-child(3) {
  margin-bottom: 10px;
}
#startScreen h1 {
  margin-top: -0.25em;
  color: white;
  text-shadow: 2px 2px #ff0000;
}
#settingScreen {
  background-color: white;
  color: #333333;
  font-family: 'VT323', monospace;
}
#ratingScreen {
  color: #333333;
  background-color: #333333;
}
#ratingScreen:hover {
  background-color:white;
}
.selecter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
table {
  width: 100vw;
  white-space: nowrap;
}
.wrapper {
  overflow-x: auto;
}
ul {
   list-style-type: none;
}
a {
  margin: 0;
  padding: 0;
  display: inherit;
}
@media screen and (orientation: portrait) {
#canvas canvas {
    width: 100vw;
}
}



